@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700&amp;display=swap);
*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: "Noto IKEA", "Noto Sans", "Roboto", "Open Sans", -apple-system,
    sans-serif !important;
}

.cart-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2em;
}

.header__logo {
  flex-shrink: 0;
  order: 2;
  display: block;
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
}

.carousel-wrapper {
  box-sizing: border-box;
}

.carousel-slide {
  padding: 2px !important;
}

.carousel__slide-show .scroll-indicator {
  padding: 2px !important;
}

.carousel__slide-show .carousel-slide > :first-child {
  max-height: inherit;
}

@media (orientation: landscape) and (max-width: 56.25em) {
  .carousel__slide-show .carousel-slide > :first-child {
    max-height: inherit;
  }
}

@media (min-width: 56.25em) {
  .header__logo {
    padding: 1.75rem 0;
  }
}

.hnf-header__logo img {
  display: block;
  width: 75px;
  height: 30px;
}

@media (min-width: 56.25em) {
  .hnf-header__logo img {
    width: 90px;
    height: 36px;
  }
}

